body {
	width: 660px;
}

header {
	margin: 10px auto 0;
	display: flex;
	justify-content: space-between;
	outline: 2px solid black;
}

header div {
	outline: 2px solid black;
}

header div:last-child {
	display: flex;
}

header div div {
	padding: 5px;
}

main {
	margin: 0 auto;
	outline: 2px solid black;
}

table {
	margin-top: 5px;
	padding: 5px;
	width: 100%;
	outline: 2px solid black;
}

.cellNeutral {
	background-color: green;
	cursor: pointer;


	&:hover {
		background-color: blue;
	}
}

.cellEmpty {
	background-color: white;
	cursor: default;
}